import React, { Component } from "react";
import { Container, Row, Col, Navbar, Nav } from "react-bootstrap";

import i18n from "../i18n";
import "./banner.scss";

class Banner extends Component {
  state = {};

  menuItems = Object.entries(i18n.MainMenu).map(([key, val]) => {
    return (
      <Nav.Item className="bn-navbar-li" key={"/" + key}>
        <Nav.Link>{val}</Nav.Link>
      </Nav.Item>
    );
  });

  render() {
    return (
      <Container id="banner">
        <Navbar fixed="top" className="bn-navbar fixed-top">
          <Nav className="bn-navbar-ic col">
            <Nav.Item>
              <Nav.Link>
                <i className="fas fa-leaf" />
              </Nav.Link>
            </Nav.Item>
          </Nav>
          <Nav className="bn-navbar-men col-md-11 col-lg-8">
            {this.menuItems}
          </Nav>
        </Navbar>
      </Container>
    );
  }
}

export default Banner;
